.loader {
  width: 96px;
  height: 96px;
  box-sizing: border-box;
  border: 6px solid #adadad;
  border-radius: 50%;
  align-items: center;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  background: #00b5cd;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, 50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

